<script setup>
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import { ref, onMounted, defineProps, watch } from "vue";

const props = defineProps({
    data:{
        type: null,
        required: true,
    }
})

const mapContainer = ref(null);
let map;
let markers = [];

const createCoustomIcon = ()=> {
    const coustomIcon = L.Icon.extend({
        options:{
            shadowUrl: '',
            // iconSize:     [38, 95],
            // shadowSize:   [50, 64],
            iconAnchor:   [22, 80],
            // shadowAnchor: [4, 62],
            popupAnchor:  [-3, -76]
        }
    })

    // return new coustomIcon({iconUrl: 'logo.png'})
    return new coustomIcon({iconUrl: 'https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon-2x.png'})
}

const addMaker = (map, latitude, longitude, station_id)=>{
   
    const marker = L.marker([latitude, longitude], {icon: createCoustomIcon()})
    .bindPopup(`第${station_id}站`)
    .addTo(map);
    
    // return marker;
}

const addLine = (first, second, map) =>{
    L.polyline([first, second]).addTo(map);
}

const initial = ()=>{   

    map = L.map(mapContainer.value, {
        center: [23.4811726, 119.5206846],
        zoom: 8,
    });

    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
        maxZoom: 18,
        // minZoom: 8,
    }).addTo(map);

    props.data.forEach((item, index) => {
        if(item.latitude == 0){
            return;  
        }
        
        addMaker(map, item.latitude, item.longitude, item.station_id);
        if( index+1 === props.data.length){
            return;
        }
        
        if(props.data[index+1].latitude != 0){
            addLine([props.data[index].latitude, props.data[index].longitude], 
            [props.data[index+1].latitude,  props.data[index+1].longitude],
            map);
        }
        
        if(index !=0 && props.data[index-1].latitude === 0){
            addLine([props.data[index-2].latitude, props.data[index-2].longitude], 
                    [props.data[index].latitude,  props.data[index].longitude],
                    map);
        }


    });
}


onMounted(() => {
    initial();
});


watch(()=>props.data,()=>{
    console.log('watch');
    markers.forEach(marker => marker.remove());
    markers = [];
    if (map) {
     map.remove();
    }
    initial();
})
</script>

<template>
    <h1>地圖導覽</h1>
    <div id="map" class="mapContainer" ref="mapContainer"></div>
</template>

