<script setup>
import { ref, } from "vue";
import OnePieceInfoModal from "@/components/OnePieceInfoModal.vue";
import TripContent from "@/components/TripContent.vue";
import TripFood from "@/components/TripFood.vue";
import TripMap from "@/components/TripMap.vue";
import {Modal} from 'bootstrap';

let jsonData = ref([]);

const currentTripJsonData = async (number) => {
  
  const data = await import(`@/jsonData/day${number}`);
  jsonData.value = [];
    
  jsonData.value = data.default;
  // Object.assign(jsonData, data.default);
};

const initial = async() =>{
  await currentTripJsonData(1);
}

const opModalRef = ref(null);

const showOpModal = () => {
    const element = document.getElementById('opInfoModal');
    opModalRef.value = new Modal(element,{
        keyboard: false,
        backdrop: 'static'
    });
    opModalRef.value.show();
};

await initial();
</script>

<template>
  <div class="container mobile-style mb-5">
    <div class="card">
      <div class="card-header d-flex justify-content-between">
        <button
          type="button"
          class="btn w-100 me-1 btn-default btn-blue-color"
          @click="currentTripJsonData(1)"
        >
          第一天
        </button>
        <button
          type="button"
          class="btn w-100 ms-1 me-1 btn-default btn-blue2-color"
          @click="currentTripJsonData(2)"
        >
          第二天
        </button>
        <button
          type="button"
          class="btn w-100 ms-1 btn-default btn-blue3-color"
          @click="currentTripJsonData(3)"
        >
          第三天
        </button>
      </div>
      <div class="card-body">
        <trip-content :data="jsonData"></trip-content>
        <trip-map :data="jsonData"></trip-map>
      </div>
      <div class="card-footer">
        <trip-food></trip-food>
      </div>
    </div>
  </div>

  <div class="one-piece-info" @click="showOpModal">
    <img src="@/assets/onePiece.svg" />
  </div>

  <one-piece-info-modal></one-piece-info-modal>
</template>


<style lang="scss" scoped>
.one-piece-info{
  position: fixed;
  bottom: 5px;
  right: 5px;

  width: 50px;
  height: 50px;
  border-radius: 50%;

  img{
    width: 100%;
    height: 100%;
  }
}
</style>
