<script setup>
import {defineProps} from 'vue';

const props = defineProps({
    info:{
        type: null,
        required: true
    }
})

const showImage = (url) => {
    return require(`@/image/${url}`);
}
</script>

<template>

    <div class="modal fade" 
         id="infoModal" 
         tabindex="-1"
    >
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Infomation</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="hideModal"></button>
                </div>
                <div class="modal-body">
                    <div v-for="item in info" :key="item">
                        <label class="mb-3" v-if="item.type === 0">{{ item.title }}{{ item.content }}</label>
                        <div class="mb-3" v-if="item.type === 1">
                            <label>{{ item.title }}</label>
                            <a :href="item.link" target="_blank">{{ item.content }}</a>
                        </div>
                        <div class="mb-3" v-if="item.type === 2">
                            <label>{{ item.title }}</label>
                            <img :src="showImage(item.url)" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<style lang="scss" scoped>
.modal-body{
    text-align: left;

    img{
        width: 100%;
        height: 100%;
    }
}
</style>