<template>
    <div class="trip-content">
        <ul class="timeline">
            <li v-for="item in data" :key="item">
                <div class="station-length" v-if="item.duration !== ''">
                    <label>{{item.duration}}</label>
                </div>
                <div class="d-flex flex-column list-div">
                    <div class="d-flex align-items-center">
                        <h5 v-if="item.station_id > -1" class="mb-0 me-2">第 {{ item.station_id }} 站</h5>
                        <label>{{ item.destination_time }}</label>
                        <div class="ms-2 info-btn"
                             type="button"
                             @click = "lookInfo(item)"
                             v-if="item.info.length > 0"
                        >i</div>
                    </div>
                    <label class="mb-2 mt-2">{{ item.station_name }}</label>
                    <label v-if="item.station_map.title !== ''">
                        －<a :href="item.station_map.url" target="_blank">{{ item.station_map.title }}</a>
                    </label>
                    <label v-for="details in item.content" :key="details">
                        {{ details }}
                    </label>
                    <!-- <label>{{ item.info }}</label> -->
                </div>
            </li>
        </ul>
    </div>
    
    <information-modal :info="infoData"></information-modal>


