</template>

<script setup>
import InformationModal from '@/components/InformationModal';
import {defineProps, watch, reactive, ref, onMounted} from 'vue';
import {Modal} from 'bootstrap';

const props = defineProps({
    data:{
        type: null,
        required: true
    }
})

const modalRef = ref(null);
let infoData = ref(null);

const lookInfo = (item)=>{
    infoData.value = null;
    infoData.value = item.info;

    if(infoData.value == null){
        return;
    }

    showModal();
}

const showModal = () => {
    const element = document.getElementById('infoModal');
    modalRef.value = new Modal(element,{
        keyboard: false,
        backdrop: 'static'
    });
    modalRef.value.show();
};

const hideModal = () => {
    // modal = new Modal(document.getElementById('infoModal'));
    modalRef.value.hide();
};

watch(()=> props,()=>{},{deep: true})
</script>

<style scoped>
.info-btn{
